import eventConfigConstant from './eventConfig.constant';

const initialState = {
  isError: false,
  eventTemplateData: [],
  mappingData: {},
};

export function eventConfigReducer(state = initialState, action) {
  const newState = { ...state };
  console.log('hfsda', action.type);

  if (action.type === eventConfigConstant.EVENT_CONFIG_SUCCESS) {
    newState.eventTemplateData = action.payload;
    console.log(newState, 'newState');
  }
  if (action.type === eventConfigConstant.EVENT_CONFIG_FAILURE) {
    newState.isError = true;
  }
  if (action.type === eventConfigConstant.EVENT_CONFIG_MAPPING_SUCCESS) {
    newState.mappingData = action.payload;
  }
  return newState;
}
