export function authHeader() {
  // return authorization header with basic auth credentials
  const user = getAuth();

  if (user && user.authdata) {
    return { Authorization: user.authdata };
  } else {
    return {};
  }
}

export function isTokenExpired() {
  var currentTime = new Date();
  const { expires_in, loginTime } = getAuth();
  var sec = Math.abs(currentTime - new Date(loginTime)) / 1000;
  console.log(sec, 'seconds');
  if (expires_in - 40 < sec) {
    return true;
  }
  return false;
}

export function setAuthorization(user) {
  localStorage.setItem('AR-user', JSON.stringify(user));
}

export function getAuth() {
  return JSON.parse(localStorage.getItem('AR-user') || '{}');
}

export function removeAuthorization() {
  localStorage.setItem('AR-user', JSON.stringify({}));
}
export function setAccessToken(user) {
  localStorage.setItem('token', user);
}

export function removeAccessToken() {
  localStorage.removeItem('token');
}

export function getToken() {
  return localStorage.getItem('token');
}
