import { put, takeLatest, call, takeEvery, delay } from 'redux-saga/effects';
import service from '../../services/service';
import environment from '../../index';
import { loaderAction } from '../loader/loader.action';

export const Enterprise_Config = {
  getEnterpriseDetails: () => {
    return { type: 'GET_ENTERPRICE' };
  },

  getSignalDetail: value => {
    return { type: 'GET_SIGNAL_DETAILS', payload: { value } };
  },
  postNewSingal: data => {
    return { type: 'POST_SIGNAL_DETAILS', payload: { data } };
  },
  postNewSubscriber: data => {
    return { type: 'POST_SUBSCRIBER_DETAILS', payload: { data } };
  },
  postNewEnterPrise: data => {
    return { type: 'POST_ENTERPRISE_DETAILS', payload: { data } };
  },
  deleteSingal: data => {
    return { type: 'DELETE_SINGAL', payload: { data } };
  },
  EDIT: data => {
    return { type: 'COPY_SINGAL_DATA', payload: data };
  },
  updateSingal: data => {
    return { type: 'EDIT_SINGAL_DATA', payload: { data } };
  },
  get_subscriber: data => {
    return { type: 'GET_SUBSCRIBER_DATA', payload: { data } };
  },

  uploadFileInfo: (file, enterpriseId, signalId) => {
    console.log('uploadedInfo');
    return {
      type: 'POST_FILE_UPLOADAA',
      payload: { file, enterpriseId, signalId },
    };
  },
  // uploadFileInfo: () => {
  //   console.log('uploadedInfo');
  //   return { type: 'POST_FILE_UPLOADAA' };
  // },
};
const { setLoader, removeLoader } = loaderAction;

function* UploadFile({ payload }) {
  const commonAction = require('../commonActivity/action').default;

  const formData = new FormData();
  formData.append('file', payload.file);
  console.log('uploadFileFunction', formData);
  yield put(setLoader());
  try {
    const response = yield call(
      service.post,
      environment.EnterpriceConfig.uploadEnterpriseFile +
        payload.enterpriseId +
        '/signal/' +
        payload.signalId,
      formData,
      { 'Content-Type': 'multipart/form-data' }
    );
    commonAction.openAlert({ title: 'Info', text: 'Uploaded successfully' });
    console.log(response, 'response');
    // if (response.status >= 200 || response.status <= 300) {
    yield put(Enterprise_Config.getEnterpriseDetails());
    yield put(Enterprise_Config.getSignalDetail(payload.enterpriseId));
    yield put(removeLoader());
    // }
    // else {
    //   throw new Error(response);
    // }
  } catch (e) {
    console.log(e);
    yield put(removeLoader());
    yield put({
      type: 'ERROR',
      error: e,
    });
  }
}

function* getEnterprise() {
  const commonAction = require('../commonActivity/action').default;
  yield put(setLoader());
  try {
    const response = yield call(
      service.get,
      environment.EnterpriceConfig.EnterpriceList
    );
    console.log(response,"urlenterprisegetresponse");
    if (response.status === 200) {
      yield put({
        type: 'ENTERPRISE_SUCCESS',
        payload: response.data,
      });
      yield put(removeLoader());
    } else {
      throw new Error(response);
    }
  } catch (e) {
    commonAction.openAlert({
      title: 'Warning',
      text: 'Network or API failure to get updated Enterprise details',
    });
    yield put(removeLoader());
    yield put({
      type: 'ERROR',
      error: e,
    });
  }
}
function* getSubscriber() {
  const commonAction = require('../commonActivity/action').default;
  yield put(setLoader());

  try {
    const response = yield call(
      service.get,
      environment.EnterpriceConfig.postSubscriber
    );
    console.log(response);
    if (response.status === 200) {
      yield put({
        type: 'GET_SUBCRIBER_DETAILS',
        payload: response.data,
      });
      yield put(removeLoader());
    } else {
      throw new Error(response);
    }
  } catch (e) {
    console.log(e);
    commonAction.openAlert({
      title: 'Warning',
      text: 'Network or API failure to Get Updated Signal Details',
    });
    yield put(removeLoader());
    yield put({
      type: 'ERROR',
      error: e,
    });
  }
}
function* getSingalEnterprise({ payload }) {
  const commonAction = require('../commonActivity/action').default;
  yield put(setLoader());

  try {
    const response = yield call(
      service.get,
      environment.EnterpriceConfig.SignalDetails + payload.value + '/signal'
    );
    console.log(response, 'enterpriseidpayload');
    if (response.status === 200) {
      yield put({
        type: 'DAVID',
        payload: response.data,
      });
      yield put(removeLoader());
    } else {
      throw new Error(response);
    }
  } catch (e) {
    console.log(e);
    commonAction.openAlert({
      title: 'Warning',
      text: 'Network or API failure to Get Updated Signal Details',
    });
    yield put(removeLoader());
    yield put({
      type: 'ERROR',
      error: e,
    });
  }
}
function* PostNewSingal({ payload }) {
  console.log(payload, 'paylioad');
  const commonAction = require('../commonActivity/action').default;
  yield put(setLoader());
  try {
    const newSignals = payload.data;
    const response = yield call(
      service.post,
      environment.EnterpriceConfig.postSignal +
        newSignals.enterpriseId +
        '/signal',
      newSignals
    );
    yield put(Enterprise_Config.getSignalDetail(newSignals.enterpriseId));
    yield put(removeLoader());
    commonAction.openAlert({
      title: 'Info',
      text: 'New Signal created',
    });
  } catch (e) {
    console.log(e);
    yield put(removeLoader());
    commonAction.openAlert({
      title: 'Warning',
      text: 'Network or API failure to send data',
    });
    yield put({
      type: 'ERROR',
      error: e,
    });
  }
}
function* PostNewSubscriber({ payload }) {
  console.log(payload, 'paylioad');
  const commonAction = require('../commonActivity/action').default;
  yield put(setLoader());
  try {
    const newSubscriber = payload.data;
    const response = yield call(
      service.post,
      environment.EnterpriceConfig.postSubscriber,
      //  +
      // newSubscriber.enterpriseId +
      //   '/signal',
      payload.data
    );
    console.log(response, 'response');
    yield put(Enterprise_Config.get_subscriber(newSubscriber));
    yield put(removeLoader());
    commonAction.openAlert({
      title: 'Info',
      text: 'New Subscriber created',
    });
  } catch (e) {
    console.log(e);
    yield put(removeLoader());
    commonAction.openAlert({
      title: 'Warning',
      text: 'Network or API failure to send data',
    });
    yield put({
      type: 'ERROR',
      error: e,
    });
  }
}
function* PostEnterpriseDetails({ payload }) {
  const commonAction = require('../commonActivity/action').default;
  console.log('postEnterprise');
  yield put(setLoader());
  try {
    const response = yield call(
      service.post,
      environment.EnterpriceConfig.postEnterPrise,
      payload.data
    );
    yield delay(1000);
    yield put(Enterprise_Config.getEnterpriseDetails());
    yield put(removeLoader());
    commonAction.openAlert({
      title: 'Info',
      text: 'Enterprise Created',
    });
  } catch (e) {
    yield put(removeLoader());
    yield put({
      type: 'ERROR',
      error: e,
    });
  }
}
function* updateSingal({ payload }) {
  const commonAction = require('../commonActivity/action').default;
  console.log(payload.data, 'paylioad');
  console.log(
    environment.EnterpriceConfig.updateSignal + payload.data.enterpriseId,
    'paylioad'
  );

  yield put(setLoader());
  try {
    const response = yield call(
      service.put,
      environment.EnterpriceConfig.updateSignal +
        payload.data.enterpriseId +
        '/signal/' +
        payload.data.signalId,
      payload.data
    );
    console.log(response, 'response');
    yield put(Enterprise_Config.getSignalDetail(payload.data.enterpriseId));
    yield put(removeLoader());
    commonAction.openAlert({
      title: 'Info',
      text: 'Updated Successfully',
    });
  } catch (e) {
    console.log(e);
    yield put(removeLoader());
    yield put({
      type: 'ERROR',
      error: e,
    });
  }
}
function* deleteSingal({ payload }) {
  yield put(setLoader());
  const commonAction = require('../commonActivity/action').default;
  console.log(payload, 'delete');
  try {
    const response = yield call(
      service.delete,
      environment.EnterpriceConfig.deleteSingal +
        payload.data.enterpriseId +
        '/signal/' +
        payload.data.signalId,
      payload.data
    );
    console.log(response, 'dltresponse');
    yield put(Enterprise_Config.getSignalDetail(payload.data.enterpriseId));
    commonAction.openAlert({
      title: 'Info',
      text: 'Deleted Successfully',
    });
    yield put(removeLoader());
  } catch (e) {
    console.log(e);
    yield put(removeLoader());
    yield put({
      type: 'ERROR',
      error: e,
    });
  }
}

export function* actionWatcherEnterPrice() {
  yield takeLatest('POST_FILE_UPLOADAA', UploadFile);
  yield takeLatest('GET_ENTERPRICE', getEnterprise);
  yield takeLatest('GET_SUBSCRIBER_DATA', getSubscriber);
  yield takeLatest('GET_SIGNAL_DETAILS', getSingalEnterprise);
  yield takeLatest('POST_SIGNAL_DETAILS', PostNewSingal);
  yield takeLatest('POST_SUBSCRIBER_DETAILS', PostNewSubscriber);
  yield takeLatest('POST_ENTERPRISE_DETAILS', PostEnterpriseDetails);
  yield takeLatest('DELETE_SINGAL', deleteSingal);
  yield takeLatest('EDIT_SINGAL_DATA', updateSingal);
}
