let data = [
  {
    enterpriseId: '',
    enterpriseName: '',
    enterpriseIATACode: '',
    enterpriseDescription: '',
  },
];

let file = null;
let signalData = [];
export const EnterpriceReducer = (state = data, action) => {
  let newState = [...state];
  switch (action.type) {
    case 'ENTERPRISE_SUCCESS':
      newState = [...action.payload];
      return newState;

    default: {
      return newState;
    }
  }
};
export const SubscriberReducer = (state = [], action) => {
  let newState = [...state];
  switch (action.type) {
    case 'GET_SUBCRIBER_DETAILS':
      newState = action.payload;
      return newState;
    default: {
      return newState;
    }
  }
};

export const EnterpriseSignal = (state = signalData, action) => {
  let newState = [...state];
  switch (action.type) {
    case 'DAVID':
      newState = action.payload;
      return newState;
    default: {
      return newState;
    }
  }
};
export const editSignalEnterprise = (state = {}, action) => {
  let newState = { ...state };
  switch (action.type) {
    case 'COPY_SINGAL_DATA':
      newState = action.payload;
      return newState;
    default: {
      return newState;
    }
  }
};
