import { put, takeLatest, call, delay } from 'redux-saga/effects';
import service from '../../services/service';
import environment from '../../index';
import eventConfigConstant from './eventConfig.constant';
import { loaderAction } from '../loader/loader.action';

const { setLoader, removeLoader } = loaderAction;
export const eventConfiguration = {
  getEvent_Template: () => {
    return { type: eventConfigConstant.ASYNC_EVENT_CONFIG_REQUEST };
  },
  updateInstance: instance => {
    return {
      type: eventConfigConstant.ASYNC_EVENT_CONFIG_UPDATEINSTANCE_REQUEST,
      payload: instance,
    };
  },
  getMappingData: (instance) => {
    return { type: eventConfigConstant.ASYNC_EVENT_CONFIG_REQUEST_MAPPINGDATA,payload:instance };
  },
};

function* getEvent_Template() {
  function success(payload) {
    return { type: eventConfigConstant.EVENT_CONFIG_SUCCESS, payload };
  }
  function failure(error) {
    return { type: eventConfigConstant.EVENT_CONFIG_FAILURE, error };
  }

  yield put(setLoader());
  try {
    const response = yield call(
      service.get,
      environment.EventConfiguration.eventtemplates
    );
    console.log('test', response);
    if (response.data) {
      yield put(success(response.data));
      yield put(removeLoader());
    } else {
      throw new Error(response);
    }
    //
  } catch (e) {
    console.log(e);
    yield put(removeLoader());
    yield put(failure(e));
  }
}

function* updateInstance({ payload }) {
  console.log(payload, 'eventsubspayload');
  const commonAction = require('../commonActivity/action').default;
  function failure(error) {
    return { type: eventConfigConstant.EVENT_CONFIG_FAILURE, error };
  }

  yield put(setLoader());
  try {
    let response, text;
    if (payload.businessEventId) {
      text = 'Updated';
      response = yield call(
        service.put,
        environment.EventConfiguration.eventtemplates +
          '/' +
          payload.businessEventId,
        payload
      );
    } else {
      text = 'Added';
      response = yield call(
        service.post,
        environment.EventConfiguration.eventtemplates,
        payload
      );
    }

    commonAction.openAlert({
      title: 'Info',
      text: `Event Config ${text} successfully`,
    });
    yield put(eventConfiguration.getEvent_Template());
    yield put(removeLoader());
  } catch (e) {
    console.log(e);
    yield put(removeLoader());
    yield put(failure(e));
  }
}

function* getMapping({payload}) {
  function failure(error) {
    return { type: eventConfigConstant.EVENT_CONFIG_FAILURE, error };
  }
  function success(payload) {
    return { type: eventConfigConstant.EVENT_CONFIG_MAPPING_SUCCESS, payload };
  }

  yield put(setLoader());
  try {
    const response = yield call(
      service.get,
      environment.EventConfiguration.mappingAPI + payload.enterpriseCode +"/signal"
    );
    if (response.data) {
      yield put(success(response.data));
    }

    yield put(removeLoader());
  } catch (e) {
    console.log(e);
    yield put(removeLoader());
    yield put(failure(e));
  }
}

export function* eventTemplateSaga() {
  yield takeLatest(
    eventConfigConstant.ASYNC_EVENT_CONFIG_REQUEST,
    getEvent_Template
  );
  yield takeLatest(
    eventConfigConstant.ASYNC_EVENT_CONFIG_UPDATEINSTANCE_REQUEST,
    updateInstance
  );
  yield takeLatest(
    eventConfigConstant.ASYNC_EVENT_CONFIG_REQUEST_MAPPINGDATA,
    getMapping
  );
}
