import { eventMonitorConstant } from './EventMonitor.constant';

let data = {};

export const EventMonitorReducer = (state = data, action) => {
  let newState = { ...state };
  switch (action.type) {
    case eventMonitorConstant.EVENT_MONITOR_SUCCESS:
      newState = { ...action.payload };
      console.log(data, 'newstate');
      return newState;

    default: {
      return newState;
    }
  }
};
