import React, { useState, useEffect } from 'react';

const Animate = ({show, enterAnimate, leaveAnimate, children, className})=>{
      const [shouldRender, setRender] = useState(show);

      useEffect(() => {
        if (show) setRender(true);
      }, [show]);
    
      const onAnimationEnd = () => {
        if (!show) setRender(false);
      };
      return (
        <>
          {shouldRender && (
            <div
              className={`${className} animation-area `}
              style={{ animation: `${show ? `${enterAnimate}` : `${leaveAnimate}`}` }}
              onAnimationEnd={onAnimationEnd}
            >
              {children}
            </div>
          )}
        </>
      );
}
 
Animate.defaultProps = {
    enterAnimate: "",
    leaveAnimate: "",
    className:""
}
export default Animate;