import React, { Suspense, lazy } from 'react';
import { Route, Switch, Router } from 'react-router-dom';
import Menu from './components/Page/Menu/Menu';
import { PrivateRoute } from './Routing/private-route';
import { history } from './Store/store';
import { InitialLoader } from './Utils/commonActivity/loader';
import CommonActivity from './Utils/commonActivity/index';

//dynamic loading

const LoginPage = lazy(() => import('./components/Page/Login/login'));
const EventConfiguration = lazy(() =>
  import('./components/Page/EventConfiguration')
);
const BusinessEvents = lazy(() => import('./components/Page/BusinessEvents1'));
const EnterpriseConfig = lazy(() =>
  import('./components/Page/Enterprise-config')
);
const EventMonitor = lazy(() =>
  import('./components/Page/EventMonitor')
);

export default function App() {
  return (
    <Router history={history}>
      <Menu />
      <CommonActivity />
      <Suspense fallback={<InitialLoader />}>
        <Switch>
          <Route path="/login" component={LoginPage} />
          <PrivateRoute
            path="/event-monitor"
            component={EventMonitor}
          />
          <PrivateRoute
            path="/event-configuration"
            component={EventConfiguration}
          />
          <PrivateRoute path="/business-events" component={BusinessEvents} />
          <PrivateRoute
            path="/enterprise-config"
            component={EnterpriseConfig}
          />
        </Switch>
      </Suspense>
    </Router>
  );
}
