import { put, takeLatest, call, delay } from 'redux-saga/effects';
import service from '../../services/service';
import environment from '../../index';
import businessConfigConstant from './businessConfig.constant';
import { loaderAction } from '../loader/loader.action';
const { setLoader, removeLoader } = loaderAction;
export const businesseventConfiguration = {
  getBusiness_Event_Template: () => {
    return { type: businessConfigConstant.ASYNC_BUSINESS_EVENT_CONFIG_REQUEST };
  },
  postBusiness_Template: obj => {
    return {
      type: businessConfigConstant.ASYNC_BUSINESS_EVENT_CONFIG_POST_REQUEST,
      payload: obj,
    };
  },
  updateBusiness_Template: instance => {
    return {
      type: businessConfigConstant.ASYNC_BUSINESS_EVENT_CONFIG_UPDATE_REQUEST,
      payload: instance,
    };
  },
};

function* getBusiness_Event_Template() {
  function success(payload) {
    return {
      type: businessConfigConstant.BUSINESS_EVENT_CONFIG_SUCCESS,
      payload,
    };
  }
  function failure(error) {
    return {
      type: businessConfigConstant.BUSINESS_EVENT_CONFIG_FAILURE,
      error,
    };
  }

  yield put(setLoader());
  try {
    const response = yield call(
      service.get,
      environment.BusinessEvents.businesseventtemplates
    );
    if (response.data) {
      yield put(success(response.data));
      yield put(removeLoader());
    } else {
      throw new Error(response);
    }
    //
  } catch (e) {
    yield put(removeLoader());
    yield put(failure(e));
  }
}

function* postBusiness_Template({ payload }) {
  try {
    var val = JSON.stringify(payload);
    const response = yield call(
      service.post,
      environment.BusinessEvents.postSchedulling,
      val
    );
  } catch (e) {
    console.log(e);
  }
}

function* updateBusiness_Template({ payload }) {
  const commonAction = require('../commonActivity/action').default;
  function failure(error) {
    return {
      type: businessConfigConstant.BUSINESS_EVENT_CONFIG_FAILURE,
      error,
    };
  }

  yield put(setLoader());
  console.log(
    service.put,
    environment.BusinessEvents.updateBusinessEvent,
    '/' + payload.businessEventTemplateId,
    payload,
    'busineseventpostcal'
  );
  try {
    let response, text;
    if (payload.businessEventTemplateId) {
      text = 'Updated';
      response = yield call(
        service.put,
        environment.BusinessEvents.updateBusinessEvent +
          '/' +
          payload.businessEventTemplateId,
        payload
      );
    } else {
      text = 'Added';
      response = yield call(
        service.post,
        environment.BusinessEvents.addBusinessEvent,
        JSON.stringify(payload)
      );
    }

    yield put(businesseventConfiguration.getBusiness_Event_Template());
    commonAction.openAlert({
      title: 'Info',
      text: `Business Event ${text} successfully`,
    });
    yield put(removeLoader());
  } catch (e) {
    yield put(removeLoader());
    yield put(failure(e));
  }
}

export function* businesseventTemplateSaga() {
  yield takeLatest(
    businessConfigConstant.ASYNC_BUSINESS_EVENT_CONFIG_REQUEST,
    getBusiness_Event_Template
  );
  yield takeLatest(
    businessConfigConstant.ASYNC_BUSINESS_EVENT_CONFIG_POST_REQUEST,
    postBusiness_Template
  );
  yield takeLatest(
    businessConfigConstant.ASYNC_BUSINESS_EVENT_CONFIG_UPDATE_REQUEST,
    updateBusiness_Template
  );
}
