import { put, takeLatest, call, takeEvery, delay } from 'redux-saga/effects';
import service from '../../services/service';
import environment from '../../index';
import { loaderAction } from '../loader/loader.action';
import { eventMonitorConstant } from './EventMonitor.constant';

export const Event_Monitor_Action = {
  getEventMonitorDetails: (FromDate, ToDate) => {
    return { type: 'GET_EVENTMONITOR_DETAILS', payload: { FromDate, ToDate } };
  },
};
const { setLoader, removeLoader } = loaderAction;
function* getEventMonitor({ payload }) {
  const commonAction = require('../commonActivity/action').default;
  yield put(setLoader());
  console.log('jhgjhg', payload.FromDate, payload.ToDate);
  try {
    const response = yield call(
      service.get,
      environment.EventMonitor.getExceptionMonitorDetails
      //  +
      //   'fromDate=' +
      //   payload.FromDate +
      //   '&toDate=' +
      //   payload.ToDate
    );
    if (response.status === 200) {
      yield put({
        type: eventMonitorConstant.EVENT_MONITOR_SUCCESS,
        payload: response.data,
      });
      yield put(removeLoader());
    } else {
      throw new Error(response);
    }
  } catch (e) {
    console.log(e);
    commonAction.openAlert({
      title: 'Warning',
      text: 'Network or API failure to get updated Exception details',
    });
    yield put(removeLoader());
    yield put({
      type: 'ERROR',
      error: e,
    });
  }
}
export function* actionWatcherEventMonitor() {
  yield takeLatest('GET_EVENTMONITOR_DETAILS', getEventMonitor);
}
