import alertConstant from './alert.constant';
import store from '../../store';

const { dispatch } = store;

const alertContent = { title: 'Alert', text: '' };

export const alertAction = {
  openAlert: (payload = alertContent) =>
    dispatch({ type: alertConstant.ALERT_OPEN, payload }),
  closeAlert: () => dispatch({ type: alertConstant.ALERT_CANCEL }),
};
