export function businesseventConfigReducer(state = initialState, action) {
  const newState = { ...state };
  console.log('hfsda', action.type);

  if (action.type === businessConfigConstant.BUSINESS_EVENT_CONFIG_SUCCESS) {
    newState.businesseventTemplateData = action.payload;
    console.log(newState, 'newState');
  }
  if (action.type === businessConfigConstant.BUSINESS_EVENT_CONFIG_FAILURE) {
    newState.isError = true;
  }
  return newState;
}
