import { combineReducers } from 'redux';
import { routerReducer } from 'react-router-redux';
import { userReducer } from './login/user.reducer';
import commonActivityReducer from './commonActivity';
import { eventConfigReducer } from './EventConfigurationPage/eventConfig.reducer';
import { businesseventConfigReducer } from './BusinessEventsPage/businessConfig.reducer';
import {
  EnterpriceReducer,
  EnterpriseSignal,
  editSignalEnterprise,
  SubscriberReducer,
  // uploadSignalEnterprise,
} from './Enterprise_Config/reducer';
import { EventMonitorReducer } from './EventMonitor/EventMonitor.reducer';

const rootReducer = combineReducers({
  authentication: userReducer,
  routing: routerReducer,
  commonActivityReducer,
  eventConfigReducer,
  businesseventConfigReducer,
  EnterpriceReducer,
  EnterpriseSignal,
  editSignalEnterprise,
  SubscriberReducer,
  EventMonitorReducer,
  // uploadSignalEnterprise,
  // businessSchedullingreducer,
});
export default rootReducer;
